@import url(https://fonts.googleapis.com/css2?family=Bitter:ital,wght@0,200;0,300;0,400;1,200;1,300;1,400&display=swap);
.results{
    margin-top: 50px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
    padding: 0 20px;
}

.results .card .overlay_div{
    position: relative;
    overflow: hidden;
}



.results .card .overlay .overview{
    font-size: 14px;
    text-align: justify;
    margin-top: 15px;
    line-height: 18px;
}

.results .card{
    width: 250px;
    margin: 30px;
    cursor: pointer;
    border-radius: 10px;
}

.results .card img{
    width:100%;
    vertical-align: bottom;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}

.results .card .captions{
    background-color: black;
    line-height: 30px;
    padding: 15px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
}

.results .card .rating i{
   color: rgb(212, 175, 55);
   margin-right: 10px;
}

@media screen and (max-width: 672px){

    .results{
        justify-content: space-around;
    }
    .results .card{
        width:150px;
        margin: 20px 10px;
    }

    html{
        font-size: 14px;
    }

}

@media screen and (max-width: 672px){

    .results .card{
        width:200px;
    }
}

.single{
    background-color: black;
    height:100vh;
    width:100vw;
    position:fixed;
    top:0;
    left:0;
    overflow: hidden;
    display: flex;
    align-items: center;
    transition: all 0.8s ease-in-out;
}

.single .container{
    width: 80%;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    border-radius: 10px;
}

.single .container .image{
    width:30%;
}

.single .container .image img{
    width:100%;
    vertical-align: bottom;
}

.single .container .content{
    padding: 20px;
    width:70%;
}

.single .container .content .links{
    margin-top: 50px;
}

.single .container .content .links a{
    text-decoration: none;
    margin-right: 15px;
    margin-top: 20px;
    color: rgb(124, 167, 218)
}

.single .container .content .subtitle{
    margin-top: 15px;
}

.single .container .content .overview{
    margin-top: 20px;
    margin-bottom: 15px;
}

.single .container .content h1{
    font-size: 36px;
}

.single .container .content p span{
    margin-right: 10px;
    color: lightgrey;
}

.single .container .content p{
    line-height: 2;
    text-align: justify;
}

.single .container .content p i{
    color: rgb(212, 175, 55);
}

.single .container .content .prod div small{
    margin-right: 10px;
    color: lightgrey;
}

.single .container .content .prod div small:after,
.single .container .content p span:after{
    content: '|';
}

.single .container .content .prod div small:last-child:after,
.single .container .content p span:last-child:after{
    content: '';
}

.single .container .content button{
    background-color: rgb(207, 0, 0);
    color: white;
    border: none;
    outline:none;
    padding: 10px 30px;
    font-size: 16px;
    border-radius: 10px;
    margin-top: 20px;
    cursor:pointer;
}

.single .container .content button:hover{
    background-color: rgb(139, 0, 0);
    transition: all 0.8s ease-in-out;
}

@media screen and (max-width: 992px){
    .single .container{
        flex-direction: column;
    }

    .single .container .image{
        width:50%;
        margin: 20px auto 0 auto;
    }

    .single .container .content{
        width:100%;
        margin: 0 auto;
    }
}

@media screen and (max-width: 590px){
    .single .container .image{
        width:90%;
    }
}

@media screen and (max-width: 380px){
    html{
        font-size: 12px;
    }

    .single .container .content h1{
        font-size: 30px;
    }
}





*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body{
    font-family: 'Bitter', serif;
    color: white;
    background: url(/static/media/background.81741049.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    height: 100vh;
    background-color: black;
}

/*---------header-----------*/

header{
    text-align: center;
    padding-top: 50px;
}

/*-------------main------------*/

main{
    padding: 30px 0;
}

main form{
    display: flex;
    justify-content: center;
    align-items: center;
}

main form input{
    width: 300px;
    padding: 10px 20px;
    border-radius: 10px;
    border: none;
    outline: none;
    font-family: 'Bitter', serif;
    font-size: 18px;
    transition: all 0.3s ease-out;
}

main form input:focus{
    box-shadow: 0px 0px 8px 3px red;
}

main form button{
    color: white;
    background: none;
    border: none;
    font-size: 20px;
    margin-left: 10px;
    outline:none;
}

main form button i:hover{
    cursor: pointer;
    color: red;
    transition: all 0.5s ease-in-out;
}

/*-----------Sort--------------*/

.sort nav{
    text-align: center;
    margin-top: 30px;
}

.sort nav a {
    margin: 0 20px;
    cursor: pointer;
}

.sort nav a:hover{
    color: red;
    transition: all 0.5s ease-in-out;
}

/*-----------Media Queries----------------*/

@media screen and (max-width: 428px){

    .sort nav a {
        margin: 0 10px;
    }

    main form input{
        width: 200px;
        font-size: 14px;
    }
}

@media screen and (max-width: 350px){

    html{
        font-size: 12px;
    }

    .sort nav a {
        margin: 0 5px;
    }
}




